import {DialogType, MessageType} from "../types/types";
import {InferActionTypes} from "./redux-store";

let initialState: InitialStateType = {
    dialogs: [
        {
            id: 1,
            name: 'Dima',
            avatarImage: 'data:image/png;base64,'
        },
        {
            id: 2,
            name: 'Vasya',
            avatarImage: 'data:image/jpeg;base64,'
        },
        {
            id: 3,
            name: 'Kolya',
            avatarImage: 'data:image/jpeg;base64,'
        },
        {
            id: 4,
            name: 'Olya',
            avatarImage: 'data:image/jpeg;base64,'
        }
    ],
    messages: [
        {id: 1, message: "Hi", isMyMessage: true},
        {id: 2, message: "How are you?", isMyMessage: false},
        {id: 3, message: "Not bad", isMyMessage: true},
    ],
};

const dialogsReducer = (state = initialState, action: ActionTypes): InitialStateType => {
    switch (action.type) {
        case "SN/DIALOGS/SEND_MESSAGE":
            let body = action.newMessageBody;
            return {
                ...state,
                messages: [...state.messages, {id: 6, message: body, isMyMessage: true}]
            };
        default:
            return state;
    }
}

export const actions = {
    sendMessage: (newMessageBody: string) => ({type: "SN/DIALOGS/SEND_MESSAGE", newMessageBody}) as const
}

export default dialogsReducer;

export type InitialStateType = {
    dialogs: Array<DialogType>,
    messages: Array<MessageType>
}
type ActionTypes = InferActionTypes<typeof actions>